:root{
	font-size: 10px;
}

body{
	overflow-x: hidden;
	margin: 0;
	font-family: 'Roboto', sans-serif;
	font-size: 1.6rem;
}

a{
	text-decoration: none;
}

ul{
	padding: 0;
	margin: 0;
}

*,
*:before,
*:after{
	box-sizing: border-box;
}

.container{
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
}

.button{
	background: none;
	outline: none;
	cursor: pointer;
	border: none;
	transition: .2s linear;
}

.input{
	outline: none;
	border-radius: 20px;
	border: none;
	font-size: 1.4rem;
}

/* Главная */

.main{
	width: 100%;
	background: url("../img/main-bg.jpg") center no-repeat;
	background-size: cover;
	padding: 2rem 0 4rem 0;
}

.main__inner{
	display: flex;
}

/* Сайдбар главной */ 

.main__bar{
	width: 28%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__content{
	width: 72%;
	margin-top: 2rem;
	position: relative;
}

.main__bar--logo{
	width: 76px;
	height: auto;
}

.main__bar--logo--img{
	width: 100%;
	height: auto;
}

.main__bar--search{
	margin-top: 3.5rem;
	position: relative;
}

.main__bar--search--input{
	width: 155px;
	box-shadow: 0 0 10px #959595;
	padding: .4rem 1rem .4rem 3rem;
}

.main__bar--search--icon{
	position: absolute;
	bottom: 2.5px;
	left: 6px;
	color: #959595;
	font-size: 2rem;
}

.main__bar--views{
	margin-top: 4rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__bar--views--items{
	text-transform: uppercase;
	color: rgba(0, 0, 0, .4);
	margin-top: 1.5rem;
	cursor: pointer;
	transition: .2s linear;
	font-weight: 500;
}

.main__bar--views--items:hover{
	color: darken(#959595, 8);
}

.main__bar--views--items--active{
	color: #00755A;
	font-weight: 700;
	position: relative;
	padding-left: 2rem;
	text-decoration: underline;
}

.main__bar--views--items--active:hover{
	color: #00755A;
}

.main__bar--views--items--active:before{
	content: "\f30b";
	display: block;
	position: absolute;
	left: 0;
	top: 1px;
	font-family: "Font Awesome 5 Free";
	color: #000;
}

/* Контент главной */ 

.main__content--nav{
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}

.main__content--nav--box{
	display: flex;
	align-items: center;
}

.main__content--nav--box div{
	text-transform: uppercase;
	cursor: pointer;
	margin-right: 3rem;
	color: rgba(255, 255, 255, .9);
}

.main__content--nav--box div:last-child{
	margin-right: 0;
}

.main__content--nav--cart{
	display: flex;
	align-items: center;
}

.main__content--nav--cart--phone{
	margin-right: 2rem;
	position: relative;
}

.main__content--nav--cart--phone--link{
	color: rgba(255, 255, 255, .9);
}

.main__content--nav--cart--phone--time{
	position: absolute;
	bottom: -20px;
	left: 4px;
	color: #fff;
	font-size: 1.2rem;
	font-weight: 100;
}

.main__content--nav--cart--inner{
	display: flex;
	align-items: center;
}

.main__content--nav--favorite--icon--inner,
.main__content--nav--cart--icon--inner{
	width: 22px;
	height: auto;
	cursor: pointer;
	position: relative;
}

.main__content--nav--favorite--icon--inner:before,
.main__content--nav--cart--icon--inner:before{
	content: "";
	display: block;
	position: absolute;
	width: 12px;
	height: 12px;
	border-radius: 50%;
	right: -5px;
	top: -3px;
	background: #fff;
}

.main__content--nav--favorite--icon--inner:after,
.main__content--nav--cart--icon--inner:after{
	content: "2";
	display: block;
	position: absolute;
	right: -2.5px;
	top: -4px;
	font-size: 1rem;
	font-weight: 500;
}

.main__content--nav--favorite--icon--inner{
	margin-right: 1.7rem;	
}

.main__content--nav--cart--icon,
.main__content--nav--favorite--icon{
	width: 100%;
	height: auto;
}

/* Цветочная карусель */

.main__carousel{
	background: url("../img/carousel-bg.png") center no-repeat;
	background-size: cover;
	position: absolute;
	top: 120px;
    right: -50px;
    width: 579px;
    height: 290px;
}

.main__carousel-2{
	position: absolute;
	background: url("../img/carousel-bg-2.png") center no-repeat;
	background-size: cover;
	top: 45%;
	left: 50%;
	transform: translateX(-50%);
	width: 360px;	
	height: 214px;
}

.main__carousel--item{
	width: 120px;
	height: auto;
	position: absolute;
	cursor: pointer;
}

.main__carousel--item--img{
	width: 100%;
	height: auto;
}

// Элементы карусели

.main__carousel--item-0{
	top: 290px;
	left: 505px;
}

.main__carousel--item-1{
	top: 120px;
	left: 505px;
}

.main__carousel--item-2{
	top: -30px;
	left: 385px;
}

.main__carousel--item-3{
	top: -55px;
	left: 180px;
}

.main__carousel--item-4{
	top: 35px;
	left: 12px;
}

.main__carousel--item-5, .main__carousel--item--null{
	top: 180px;
	left: 0;
}

/* Главный цветок */ 
.main__carousel--item--main{
	width: 240px;
	height: auto;
	position: absolute;
	left: 177px;
	top: 185px;
	transition: .2s linear;
	z-index: 5;
}

.main__fade{
	opacity: 0;
}

.main__carousel--item--img{
	width: 100%;
	height: auto;
	transition: .3s linear;
}

.main__carousel--arrow{
	width: 35px;
	height: 35px;
	border-radius: 50%;
	background: #00755A;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: .2s linear;
	cursor: pointer;
	z-index: 5;
}

.main__carousel--arrow:hover{
	opacity: .7;
}

.main__carousel--arrow--left{
	bottom: -50px;
	left: 75px;
}

.main__carousel--arrow--right{
	bottom: -50px;
	right: 75px;
}

.main__carousel--arrow--icon{
	color: #fff;
	font-size: 2rem;
}

.white{
	position: absolute;
	bottom: -187px;
	right: -63px;
	width: 200px;
	height: 200px;
	z-index: 4;
	background: #fff;
	transform: rotate(4deg);
}

.white-2{
	z-index: 4;
	width: 200px;
	height: 200px;
	background: #fff;
	position: absolute;
	transform: rotate(21deg);
	left: -83px;
    bottom: -69px;
}

// Анимации для элементов карусели

.animate__1{
	animation: animate1 .5s linear;
	animation-fill-mode: forwards;
}

.animate__2{
	animation: animate2 .5s linear;
	animation-fill-mode: forwards;
}

.animate__3{
	animation: animate3 .5s linear;
	animation-fill-mode: forwards;
}

.animate__4{
	animation: animate4 .5s linear;
	animation-fill-mode: forwards;
}

.animate__5{
	animation: animate5 .5s linear;
	animation-fill-mode: forwards;
}

@keyframes animate1{
	0%{
		top: 120px;
		left: 505px;
	}
	50%{
		left: 530px;
	}
	100%{
		top: 290px;
		left: 505px;
	}
}

@keyframes animate2{
	0%{
		top: -30px;
		left: 385px;
	}
	50%{
		left: 460px;
	}
	100%{
		top: 120px;
		left: 505px;
	}
}

@keyframes animate3{
	0%{
		top: -55px;
    	left: 180px;
	}
	50%{
		top: -50px;
	}
	100%{
		top: -30px;
		left: 385px;
	}
}

@keyframes animate4{
	0%{
    	top: 35px;
		left: 12px;
	}
	50%{
		top: -30px;
	}
	100%{
		top: -55px;
    	left: 180px;
	}
}

@keyframes animate5{
	0%{
    	top: 180px;
    	left: 0;
	}
	50%{
		left: -20px;
	}
	100%{
		top: 35px;
		left: 12px;
	}
}

// Карусель в обратную сторону

.animatereturn0{
	animation: animatereturn0 .5s linear;
	animation-fill-mode: forwards;
}

.animatereturn1{
	animation: animatereturn1 .5s linear;
	animation-fill-mode: forwards;
}

.animatereturn2{
	animation: animatereturn2 .5s linear;
	animation-fill-mode: forwards;
}

.animatereturn3{
	animation: animatereturn3 .5s linear;
	animation-fill-mode: forwards;
}

.animatereturn4{
	animation: animatereturn4 .5s linear;
	animation-fill-mode: forwards;
}

@keyframes animatereturn0{
	0%{
		top: 290px;
    	left: 505px;
	}
	50%{
		left: 530px;
	}
	100%{
		top: 120px;
    	left: 505px;
	}
}

@keyframes animatereturn1{
	0%{
		top: 120px;
    	left: 505px;
	}
	50%{
		top: 0px;
	}
	100%{
		top: -30px;
    	left: 385px;
	}
}

@keyframes animatereturn2{
	0%{
		top: -30px;
    	left: 385px;
	}
	50%{
		top: -75px;
	}
	100%{
		top: -55px;
		left: 180px;
	}
}

@keyframes animatereturn3{
	0%{
		top: -55px;
		left: 180px;
	}
	50%{
		top: -30px;
	}
	100%{
		top: 35px;
    	left: 12px;
	}
}

@keyframes animatereturn4{
	0%{
		top: 35px;
    	left: 12px;
	}
	50%{
		left: -30px;
	}
	100%{
		top: 180px;
    	left: 0;
	}
}





// Текст под букетом карусели

.main__carousel--item--text--inner{
	position: absolute;
	bottom: -235px;
	left: 190px;
	z-index: 5;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	transition: .2s linear;
}

.main__carousel--text--view{
	font-size: 1.8rem;
	color: rgba(0, 0, 0, .9);
}

.main__carousel--text--name{
	font-size: 1.3rem;
	color: rgba(0, 0, 0, .6);
	margin-top: .4rem;
}

.main__carousel--text--box{
	margin-top: 1.8rem;
	display: flex;
	align-items: center;
}

.main__carousel--text--price{
	font-size: 2.8rem;
	font-weight: 500;
	color: #00755A;
	margin-right: 1.5rem;
}

.main__carousel--text--buy--button{
	font-size: 1rem;
	text-transform: uppercase;
	color: #fff;
	padding: .6rem 2rem;
	background: #00755A;
	border-radius: 1rem;
}

.main__carousel--text--buy--button:hover{
	opacity: .7;
}












/* Затемнение фона */

.grey{
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.34);
	position: fixed;
	top: 0;
	left: 0;
	display: none;
}